import React, { Component } from 'react'


class About extends Component {
   
    render() {
      let resumeData = this.props.resumeData;
      return (
     <section id="about">
        <div className="row">
          <div className="three columns">
            <img className="profile-pic" src="images/profile.jpg" alt />
          </div>
          <div className="nine columns main-col">
            <h2>About Me</h2>
            <p>
            {
              resumeData.aboutme
              }
            </p>
            <div className="row">
              <div className="columns contact-details">
                <h2>Contact Details</h2>
                <p className="address">
                  <span>{resumeData.name}</span><br />
                  <span>{resumeData.address}<br />
                  </span><br />
                  <span>{resumeData.telephone}</span><br />
                  <span>{resumeData.email}</span>
                </p>
              </div>
              <div className="columns download">
                <p>
                  <a href="https://drive.google.com/open?id=1DPI4-Q0kIFCr4r5CfZGQjeNvYR3rUoHj" className="button"><i className="fa fa-download" />Download Resume</a>
                </p>
              </div>
            </div> {/* end row */}
          </div> {/* end .main-col */}
        </div>
      </section>
      );
    }
  }
  
  export default About;